import React from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";
import DeeplinkHandle from "./DeeplinkHandle";
import ActivateAccount from "./ActivateAccount";

export default function navigation() {
  return (
    <Router>
      <Switch>
        <Route exact path="/verify-email/:uuid">
          <ActivateAccount />
        </Route>
        <Route path="/:bucketId">
          <DeeplinkHandle />
        </Route>
      </Switch>
    </Router>
  );
}