import React, {useEffect} from 'react';
import {useParams} from 'react-router-dom';

export default function DeeplinkHandle() {
  let { bucketId } = useParams();
  useEffect(()=>{
    // const bucketId = window.location.pathname.split("/")[1];
    window.location.replace(
      `buckets://bucket/${bucketId}`
    );
  }, []);

  setTimeout(function () {
    const isIos = !!navigator.platform && /iPad|iPhone|iPod/.test(navigator.platform);
    if(isIos) {
      window.location.replace("https://www.apple.com/app-store");
      // window.location.replace("intent:#Intent;scheme=market://;end");
    } else {
      // window.location.replace("https://play.app.goo.gl/?link=https://play.google.com/store/apps/details?id=com.myapp");
      window.location.replace("https://play.app.goo.gl/?link=https://play.google.com/store/apps");
    }
  }, 2000);

  return null;
}